.formRegistration {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    background: #232526;
    background: -webkit-linear-gradient(to right, #414345, #232526);
    background: linear-gradient(to right, #414345, #232526);
    border: 1px solid gold;
    gap: 10px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.formRegistrationTitle {
    display: flex;
    justify-content: center;
    align-items: center;
}

.feedbackFormGroup {
    width: 100%;
}

.inputName,
.inputEmail,
.inputPassword {
    box-sizing: border-box;
    width: 100%;
    height: 30px;
    padding: 5px;
    background-color: transparent;
    border: 1px solid gold;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    outline: none;
}

.inputName::placeholder,
.inputEmail::placeholder,
.inputPassword::placeholder {
    opacity: 0.4;
}

.btnwrapper {
    width: 100%;
    display: flex;
    gap: 10px;
}

.btnRegister,
.btnLogIn {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 30px;
    padding: 5px;
    background-color: transparent;
    border: 1px solid gold;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    cursor: pointer;
}